<template>
  <v-container grid-list-xs>
    <v-text-field label="Message" id="id" v-model="message" @keydown.enter="sendMessage"></v-text-field>
    <v-btn color="success" @click="sendMessage">
      Send &nbsp;&nbsp;
      <v-icon>send</v-icon>
    </v-btn>

    <v-toolbar flat color="white">
      <v-toolbar-title>Patients</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>

    <v-data-table :headers="headers" :items="patients" :expand="expand" item-key="name">
      <template v-slot:items="props">
        <tr
          @click="props.expanded = !props.expanded;resetChart();joinRoom(patients.indexOf(props.item))"
        >
          <td class="text-xs-center">{{props.item.id}}</td>
          <td class="text-xs-center">{{ props.item.name }}</td>
          <td class="text-xs-center">{{ props.item.heartrate }}</td>
          <td class="text-xs-center">{{ props.item.temp }}</td>
        </tr>
      </template>
      <template v-slot:expand="props">
        <v-card flat>
          <v-container grid-list-xs>
            <div id="chart">
              <apexchart
                ref="realtimeChart"
                type="line"
                height="350"
                :options="chartOptions"
                :series="series"
              />
            </div>
          </v-container>
        </v-card>
      </template>
    </v-data-table>
  </v-container>
</template>
<script>
import axios from "axios";

var lastDate = 0;
var data = [];

function getDayWiseTimeSeries(baseval, count, yrange) {
  var i = 0;
  while (i < count) {
    var x = baseval;
    var y =
      Math.floor(Math.random() * (yrange.max - yrange.min + 1)) + yrange.min;

    data.push({
      x,
      y
    });
    lastDate = baseval;
    baseval += 86400 / 24;
    i++;
  }
}

function updateChart(y) {
  let newDate = lastDate + 86400 / 24;
  lastDate = newDate;
  data.push({
    x: newDate,
    y: y
  });
}
getDayWiseTimeSeries(new Date().getTime(), 1, {
  min: 10,
  max: 90
});

function getNewSeries(baseval, yrange) {
  var newDate = baseval + 86400 / 24;
  lastDate = newDate;
  data.push({
    x: newDate,
    y: Math.floor(Math.random() * (yrange.max - yrange.min + 1)) + yrange.min
  });
}

function resetData() {
  data = data.slice(data.length - 10, data.length);
}
export default {
  data: () => ({
    expand: false,
    headers: [
      {
        text: "#",
        align: "center",
        sortable: false,
        value: "id"
      },
      {
        text: "Name",
        align: "center",
        sortable: false,
        value: "name"
      },
      {
        text: "Heart Rate",
        align: "center",
        sortable: false,
        value: "heartrate"
      },
      {
        text: "Tempreture",
        align: "center",
        sortable: false,
        value: "temp"
      }
    ],
    message: "",
    logs: [],
    patients: [],
    status: "disconnected",
    currentPatient: {},
    chartOptions: {
      chart: {
        animations: {
          enabled: true,
          easing: "linear",
          dynamicAnimation: {
            speed: 20
          }
        },
        toolbar: {
          show: false
        },
        zoom: {
          enabled: false
        }
      },
      dataLabels: {
        enabled: false
      },
      stroke: {
        curve: "smooth",
        width: 2,
      },

      title: {
        text: `EMG Signal`,
        align: "left"
      },
      markers: {
        size: 0
      },
      xaxis: {
        type: "datetime",
        range: 777600,
        labels: {
          show: false
        }
      },
      yaxis: {
        max: 5,
        min: 0,
        forceNiceScale : true,
      },
      legend: {
        show: false
      }
    },
    series: [
      {
        data: data.slice()
      }
    ]
  }),
  methods: {
    getPatients: function() {
      axios
        .get("api/patients")
        .then(response => {
          this.patients = response.data;
          for (let index = 0; index < this.patients.length; index++) {
            const id = this.patients[index].id;
            this.$socket.sendObj(
              JSON.stringify({
                command: "join",
                patient: id
              })
            );
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    sendMessage: function() {
      console.log("sending");
      this.$socket.sendObj(
        JSON.stringify({
          command: "send",
          patient: this.currentPatient.id,
          message: this.message
        })
      );
      this.message = "";
    },
    messageReceived: function(data) {
      let new_msg = JSON.parse(data.data);
      if (new_msg.type == "message") {
        let patient_id = new_msg.room;
        let patient = this.patients.filter(function(element) {
          return element.id === patient_id;
        });
        patient = patient[0];
        patient.temp = new_msg.message.temp;
        patient.heartrate = new_msg.message.heartrate;
        if (
          // parseFloat(new_msg.message.emg) &&
          patient_id == this.currentPatient.id
        ) {
          let emg = JSON.parse(new_msg.message.emg);
          for (let index = 0; index < emg.length; index++) {
            const element = emg[index];
            updateChart(element);
          }
        }
      }
    },
    joinRoom: function(index) {
      if (this.currentPatient.id === this.patients[index].id) {
        this.currentPatient = {};
      } else {
        this.currentPatient.id = this.patients[index].id;
        this.currentPatient.name = this.patients[index].name;
        this.currentPatient.doctor = this.patients[index].doctor;
      }
    },
    resetChart: function() {
      data = [];
      getDayWiseTimeSeries(new Date().getTime(), 1, {
        min: 10,
        max: 90
      });
    },
    intervals: function() {
      var me = this;
      window.setInterval(function() {
        if (me.$refs.realtimeChart)
          me.$refs.realtimeChart.updateSeries([
            {
              data: data
            }
          ]);
      }, 20);

      // every 60 seconds, we reset the data to prevent memory leaks
      // window.setInterval(function() {
      //   resetData();
      //   if (me.$refs.realtimeChart) {
      //     me.$refs.realtimeChart.updateSeries(
      //       [
      //         {
      //           data
      //         }
      //       ],
      //       false,
      //       true
      //     );
      //   }
      // }, 60000);
    },
    leaveAllRooms: function() {
      for (let index = 0; index < this.patients.length; index++) {
        const id = this.patients[index].id;
        this.$socket.sendObj(
          JSON.stringify({
            command: "leave",
            patient: id
          })
        );
      }
    }
  },
  mounted() {
    this.getPatients();
    this.intervals();
  },
  beforeMount() {
    this.$socket.onmessage = data => this.messageReceived(data);
  },
  beforeDestroy() {
    this.leaveAllRooms();
  }
};
</script>

